/** Media Queries **/
.text-center {
  text-align: center;
}

:root {
  --modal-duration: 1s;
}

.boton {
  position: relative;
  background-color: rgba(255, 255, 255, 0.315);
  padding: 1rem 3rem;
  color: #FFFFFF;
  box-shadow: 1px 1px 30px -5px #5880f6;
  margin-top: 2rem;
  font-size: 1.8rem;
  border-radius: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition: all 0.15s ease-in-out;
  text-align: center;
  text-decoration: none;
  letter-spacing: 1px;
  overflow: hidden;
}
.boton span {
  position: relative;
  z-index: 2;
}
.boton::before {
  content: "";
  position: absolute;
  top: var(--y);
  left: var(--x);
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  border-radius: 50px;
  background: #412EC2;
  z-index: 0;
  transition: all 0.7s;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  transition: width 0.15s, height 0.15s;
  box-shadow: 1px 1px 30px -5px #449de6;
}
.boton:hover::before {
  transition: all 0.7s;
  width: 400px;
  height: 300px;
  border-radius: 50%;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal .modal-content {
  margin: 10% auto;
  width: 500px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
  animation-name: modalopen;
  animation-duration: var(--modal-duration);
}
.modal .modal-header {
  background: #412EC2;
  padding: 1rem;
  color: #fff;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
}
.modal .modal-header h2 {
  font-size: 2rem;
}
.modal .closeModal {
  font-size: 3.5rem;
  cursor: pointer;
}
.modal .modal-body {
  padding: 10px 20px;
  background: #01081f;
}
.modal .modal-footer {
  background: #412EC2;
  padding: 10px;
  color: #fff;
  text-align: center;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}
.modal .modal-footer h3 {
  font-size: 2rem;
}

.modal-header h2,
.modal-footer h3 {
  margin: 0;
}

.close {
  color: #ccc;
  float: right;
  font-size: 30px;
  color: #fff;
}
.close:hover, .close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
@keyframes modalopen {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}